<template>
  <div>
    <el-card header="" class="login-card">
      <!--native:  prevent  url-->
      <el-form @submit.native.prevent="login">
        <el-form-item label="">
          <el-input v-model="model.username"></el-input>
        </el-form-item>
        <el-form-item label="" style="margin-top: -1rem">
          <el-input type="password" v-model="model.password"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" native-type="submit"></el-button>
        </el-form-item>
      </el-form>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      model: {}
    };
  },
  methods: {
    async login() {
      const res = await this.$http.post("login", this.model);
      // sessionStorage.token = res.data.token;
      localStorage.token = res.data.token;
      this.$router.push("/");
      this.$message({
        type: "success",
        message: ""
      });
    }
  }
};
</script>

<style>
.login-card {
  width: 25rem;
  margin: 5rem auto;
  background-color: #bbb;
}
</style>
